import React, { Component } from 'react';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import { Link } from "react-router-dom";
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CircularProgress from '@material-ui/core/CircularProgress';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

export default class Event extends Component {

  constructor() {
    super();
    this.state = {
       activeStep: 0,
       steps: [],
       title: ""
    }
  }

  getId = () => {
    let parts = document.URL.split('/');
    let url = parts.pop();
    return !url ? parts.pop(): url;
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_URL}/api/test/${this.getId()}`, {
        mode: 'cors'
      }
    ).then(res => {
      return res.json();
    }).then(val => {
      console.log(val);
      let steps = val.images.map((src, ind) => {
        return {imgPath: {background: `url(${src.image}) center no-repeat`, backgroundSize: 'contain'}, label: `Картинка ${ind}`}
      });
      let maxSteps = steps.length;
      console.log(steps);


      this.setState({
        title: val.title,
        steps: steps,
        maxSteps: maxSteps,
        body: val.body_Text,
        place: val.place.address,
      })
    });
  }

   handleNext = () => {
     this.setState(prevState => ({
       activeStep: prevState.activeStep + 1,
     }));
   };

   handleBack = () => {
     this.setState(prevState => ({
       activeStep: prevState.activeStep - 1,
     }));
   };

   handleStepChange = activeStep => {
     this.setState({ activeStep });
   };

   render() {



     return (
       <div className='back'>
       <Link to='/' className='no-style'>
         <Button size="small" color="primary" >
         <KeyboardArrowLeft />
           назад
          </Button>
        </Link>
       <Card className='event'>


         <CardHeader
          title={this.state.title.toUpperCase() || <CircularProgress disableShrink />}
          className='event__header'
          />

         <AutoPlaySwipeableViews
          axis='x'
          className='event__images_width'
          index={this.state.activeStep}
          onChangeIndex={this.handleStepChange}
          enableMouseEvents
        >
         {this.state.steps.map((step, index) => (
           <div key={step.label}>
           {Math.abs(this.state.activeStep - index) <= 2 ? (
             <div className='event__images' style={step.imgPath} ></div>
           ) : null}
           </div>
         ))}
         </AutoPlaySwipeableViews>
         {this.state.maxSteps && <MobileStepper
           steps={this.state.maxSteps}
           position="static"
           activeStep={this.state.activeStep}
           nextButton={
             <Button size="small" onClick={this.handleNext} disabled={this.state.activeStep === this.state.maxSteps - 1}>
               <KeyboardArrowRight />
             </Button>
           }
           backButton={
             <Button size="small" onClick={this.handleBack} disabled={this.state.activeStep === 0}>
              <KeyboardArrowLeft />

             </Button>
           }
         />}

       <p className='event__text'>
        {this.state.body && this.state.body.replace(/\<.*?\>/g, ' ')}
       </p>
       <div className="event__address">{'Адрес: ' + this.state.place}</div>
       </Card>
       </div>
     );
   }
 }
